<template>
  <div class="user-wrap">
    <van-nav-bar
      title="意见反馈"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-field
        readonly
        clickable
        label="意见类型"
        :value="type"
        placeholder="选择意见类型"
        @click="showPicker = true"
      />
      <van-popup v-model="showPicker" position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
      <van-field
        v-model="message"
        rows="3"
        autosize
        label="意见反馈"
        type="textarea"
        maxlength="150"
        placeholder="请输入留言"
        show-word-limit
      />
      <van-field name="uploader" label="上传附件">
        <template #input>
          <van-uploader v-model="uploader" />
        </template>
      </van-field>
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit">
          确定
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  name: 'callback',
  components: {
  },
  data () {
    return {
      message: '',
      type: '',
      showPicker: false,
      columns: ['a', 'b', 'c', 'd', 'e'],
      uploader: []
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    onConfirm(value) {
      this.type = value
      this.showPicker = false
    },
    onSubmit(values) {
      console.log('submit', values);
    }
  }
}
</script>



